<template>
  <AppBorder :app-info="Brower" herder-css="header1" :size="size">
    <template v-slot:header>
      <div class="fx-ac">
        <div>
          <span class="fz22">{{Brower.icon}}</span>
        </div>
        <div>
          <span></span>
        </div>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver" style="height: 100%">
        <div class="fx-ac urlBox">
          <input class="urlInput" type="text" v-model="input" @keyup.enter="search">
        </div>
        <div style="height: 100%;background-color: #f8f9fa">
          <iframe id="iframe" :src="src" width="100%" height="100%"></iframe>
        </div>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import pageManager from "@/utils/PageManager";

export default {
  name: "Brower",
  components: {AppBorder},
  data() {
    return {
      Brower: pageManager.getAppInfo('Brower'),
      src: 'about:blank',
      input: 'www.bing.com',
      size: {
        width: '80%',
        height: '90%'
      }
    }
  },
  mounted() {
    this.search();
  },
  methods: {
    search() {
      if (this.input.startsWith('http')) {
        this.src = this.input;
      } else {
        this.src = 'https://' + this.input;
      }
    },
    search1(url) {
      this.input = url;
      if (url.startsWith('http')) {
        this.src = url;
      } else {
        this.src = 'https://' + url;
      }
    },
  }
}
</script>

<style scoped>

.urlInput {
  width: 80%;
}

.urlBox {
  background-color: #f8f9fa;
}
</style>
