import textHandler from "./textHandler";
import binaryHandler from "./binaryHandler";

class WsClient {
  constructor() {
    this.client = null;
    this.groupMsgList = [];
    this.memberList = {
      list: []
    };
    this.headImgMap = {};
    this.nickNameMap = {};
    this.self = {
      clientName: '---',
      status: 'offline',
      curTalk: 'all'
    };
    this.privateMsgMap = {};
    this.unreadMsgCountMap = {
      'all': 0
    }
    this.chatRoom = null;
    this.lastKey = 0;
    this.callBackMap = new Map();
  }

  getConnection(connName) {
    const http = require("../../axios/index");
    let token = localStorage.getItem("token");
    if (token) token = '?token=' + token;
    else token = '';
    this.client = new WebSocket(http.default.WSBaseUrl + token);


    //open event
    this.client.onopen = () => {
      console.log(connName + " open websocket...");
    };

    //close event
    this.client.onclose = () => {
      this.clearAllStatus();
      console.log(connName + " close websocket...");
    };

    this.client.onerror = () => {
      this.clearAllStatus();
      console.log(connName + " error...");
    }
  }

  clearAllStatus() {
    this.self.clientName = '---';
    this.self.status = 'offline';
    this.self.curTalk = 'all';
    this.memberList.list = [];
    this.groupMsgList.length = 0;
    this.client = null;
  }

  close() {
    this.client.close();
  }

  onmessage() {
    this.client.onmessage = (evt) => {
      const that = this.chatRoom;
      if (typeof evt.data == 'string') {
        textHandler(that, evt.data);
      } else {
        binaryHandler(that, evt.data);
      }
    }
  }

  setChatRoom(that) {
    this.chatRoom = that;
  }

  checkUnread(that, name) {
    if (that.self.curTalk !== name)
      that.unreadMsgCountMap[name]++;
    else
      setTimeout(() => {
        that.talkBox.scrollTop = that.talkBox.scrollHeight;
      }, 250);
  }

  generateKey() {
    let key = Date.now(), lastKey = this.lastKey instanceof Number ? this.lastKey : 0;
    if (key <= lastKey) {
      key = lastKey + 1;
    }
    this.lastKey = key;
    return key;
  }

  sendData(Msg, ...args) {
    let callback, binary;
    let msg = {...Msg, sendTime: this.generateKey(), needAck: false}
    if (args[args.length - 1] instanceof Function) {
      callback = args.pop();
      msg['needAck'] = true;
      this.callBackMap.set(String(msg.sendTime), callback);
    }
    if (args[args.length - 1] instanceof Blob) binary = args.pop();
    this.writeData(msg, binary);
  }

  writeData(Msg, binary) {
    Msg = JSON.stringify(Msg);
    if (binary) {
      const buf1 = Buffer.from(Msg);
      let len = new ArrayBuffer(4);
      new DataView(len).setInt32(0, buf1.length, false);
      let list = [len, buf1];
      binary && list.push(binary);
      this.client.send(new Blob(list));
    } else this.client.send(Msg);
  }

  getMessageList() {
    return this.groupMsgList;
  }

  getMemberList() {
    return this.memberList;
  }

  getNickNameMap() {
    return this.nickNameMap;
  }

  getHeadImgMap() {
    return this.headImgMap;
  }

  getSelf() {
    return this.self;
  }

  setSelf(name) {
    this.self.clientName = name;
    this.self.status = 'onLine';
  }

  getPrivateMsgMap() {
    return this.privateMsgMap;
  }

  getUnreadMsgCountMap() {
    return this.unreadMsgCountMap;
  }
}

const client = new WsClient();
global.client = client;
export default client