<template>
  <AppBorder :app-info="Live2DShow" :herder-css="'header1'" :size="size">
    <template v-slot:header>
      <div class="fx-ac">
        <span class="fz22">{{ Live2DShow.icon }}</span>
        <span>{{ Live2DShow.CName }}</span>
      </div>
    </template>
    <template v-slot:main>
      <div id="L2DContent" class="L2DContent" @mousemove="mousemove">
        <img id="lastImg" class="lastImg" src="../../assets/last.png" @click="lastModel" v-show="lastImg" width="50"
             height="100" alt="">
        <img id="nextImg" class="nextImg" src="../../assets/next.png" @click="nextModel" v-show="nextImg" width="50"
             height="100" alt="">
        <div id="live2dDiv" class="fx-pc">
        </div>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import pageManager from "@/utils/PageManager";
import '/public/live2d.min'
import '/public/pixi.min';
import '/public/live2dv3.min';

export default {
  name: "Live2DShow",
  components: {AppBorder},
  created() {
    this.modelList = require('../../assets/modelList').default;
  },
  mounted() {
    this.live2dBox = document.getElementById('live2dDiv');
    this.nextModel();
  },
  data() {
    return {
      isV2: true,
      Live2DShow: pageManager.getAppInfo('Live2DShow'),
      live2dBox: null,
      size: {
        width: '400px',
        height: '600px'
      },
      modelList: null,
      currIdx: 0,
      lastImg: false,
      nextImg: false
    }
  },
  methods: {
    nextModel() {
      this.currIdx = ++this.currIdx % this.modelList.length;
      this.loadModel(this.modelList[this.currIdx]);
    },
    lastModel() {
      this.currIdx = (--this.currIdx + this.modelList.length) % this.modelList.length;
      this.loadModel(this.modelList[this.currIdx]);
    },
    loadModel(modelPath) {
      let idx1 = modelPath.lastIndexOf('/');
      let idx2 = modelPath.lastIndexOf('/', idx1 - 1);
      let modelName = modelPath.substring(idx2 + 1, idx1);
      this.$message.info("" + modelName);
      if (modelPath.lastIndexOf('model3') !== -1) {
        let basePath = modelPath.substring(0, idx2 + 1);
        this.live2dv3(basePath, modelName);
      } else {
        this.live2dv2(modelPath)
      }
    },
    live2dv3(basePath, modelName) {
      let domRect = document.getElementById("Live2DShow").getBoundingClientRect();
      let width = Math.floor(domRect.width);
      let height = Math.floor(domRect.height);
      this.isV2 = false;
      this.live2dBox.innerHTML = '';
      new window.l2dViewer(
          {
            el: this.live2dBox,
            basePath: basePath,
            modelName: modelName,
            'width': width,
            'height': height
          }
      );
    },
    live2dv2(modelPath) {
      let domRect = document.getElementById("Live2DShow").getBoundingClientRect();
      let width = Math.floor(domRect.width);
      let height = Math.floor(domRect.height);
      this.isV2 = true;
      this.live2dBox.innerHTML = '';
      let cvs = document.createElement('canvas');
      cvs.setAttribute("id", "live2d");
      cvs.setAttribute("width", width);
      cvs.setAttribute("height", height);
      this.live2dBox.appendChild(cvs);
      this.$nextTick(() => {
        window.loadlive2d('live2d', modelPath);
      })
    },
    onResize() {
      this.loadModel(this.modelList[this.currIdx]);
    },
    mousemove(ev) {
      if (ev.target.id === 'nextImg' || ev.target.id === 'lastImg') return;
      let tmp = ev.offsetX < ev.currentTarget.clientWidth / 4;
      if (this.lastImg !== tmp) this.lastImg = tmp;
      tmp = ev.offsetX > (ev.currentTarget.clientWidth / 4 * 3);
      if (this.nextImg !== tmp) this.nextImg = tmp;
    },
    loadExternalResource(url, type) {
      return new Promise((resolve, reject) => {
        let tag;
        if (type === "css") {
          tag = document.createElement("link");
          tag.rel = "stylesheet";
          tag.href = url;
        } else if (type === "js") {
          tag = document.createElement("script");
          tag.src = url;
        }
        if (tag) {
          tag.onload = () => resolve(url);
          tag.onerror = () => reject(url);
          document.head.appendChild(tag);
        }
      });
    },
  }
}
</script>

<style scoped>

.L2DContent {
  border: #3a8ee6 1px solid;
  height: 100%;
}

.lastImg {
  position: absolute;
  top: 40%;
  left: 3px;
}

.nextImg {
  position: absolute;
  top: 40%;
  right: 3px;
}
</style>