<template>
  <AppBorder :app-info="AMap" :size="size">
    <template v-slot:header>
      <div class="fx-ac fx-pj" style="width: 100%">
        <span class="fz22 fx-pac">{{ AMap.icon }}</span>
        <span style="color: white">{{ AMap.CName }}</span>
        <span></span>
      </div>
    </template>
    <template v-slot:main>
      <div id="container"></div>
      <div class="searchBox">
        <input id="key-input" placeholder="" @mousemove="ev=>ev.stopPropagation()" @keydown.enter="searchNear"/>
      </div>
      <div class='input-card fx-pj fx-ver'>
        <span><input type="checkbox" checked @click="toggleSome($event,'scale')"/></span>
        <span><input type="checkbox" checked @click="toggleSome($event,'toolBar')"/></span>
        <span><input type="checkbox" checked @click="toggleSome($event,'controlBar')"/></span>
        <span><input type="checkbox" checked @click="toggleSome($event,'overview')"/></span>
        <span><input type="checkbox" @click="toggleSome($event,'satellite')"/></span>
        <span><input type="checkbox" @click="toggleSome($event,'mouseTool')"/></span>
      </div>
      <div class="measureTool fx-pj" v-if="measureToolShow">
        <span> <input type="radio" name="func" @change="changeTool('rule')"></span>
        <span><input type="radio" name="func" @change="changeTool('measureArea')"></span>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from "@/components/AppBorder";
import pageManager from "@/utils/PageManager";
import AMapLoader from "@amap/amap-jsapi-loader";

window._AMapSecurityConfig = {
  securityJsCode: '7bd70508203e8cf94e2f4bae54df4115',
}

export default {
  name: "AMap",
  components: {AppBorder},
  data() {
    return {
      AMap: pageManager.getAppInfo("AMap"),
      size: {width: '1000px', height: '700px'},
      map: null,
      makerMap: new Map(),
      measureToolShow: false
    }
  },
  mounted() {
    this.initMap();
    window.amap = this;
  },
  methods: {
    initMap() {
      let reloadCount = 1;
      if (!this.count) this.count = () => {
        console.log('' + reloadCount + '');
        return ++reloadCount;
      }
      AMapLoader.load({
        key: 'acbaa25e5fc310dccfeeb1909ae0ce85',  //key
        version: "2.0",
        plugins: ['AMap.ToolBar', 'AMap.Scale', 'AMap.ControlBar', 'AMap.PlaceSearch', 'AMap.HawkEye', 'AMap.AutoComplete', 'AMap.TileLayer', 'AMap.MouseTool'],
        AMapUI: {
          version: "1.1",
          plugins: [],
        },
        Loca: {
          version: "2.0"
        },
      }).then((AMap) => {
        this.map = new AMap.Map("container", {
          viewMode: "3D",
          zoom: 14,
          zooms: [2, 22],
          center: [104.065100, 30.657236],
        });
        window.map = this.map;
        this.initPlugins(AMap);
      }).catch(e => {
        console.log(e);
        if (this.count() <= 3) this.$nextTick(() => this.initMap());
      });
    },
    initPlugins(AMap) {
      //
      this.scale = new AMap.Scale();
      //
      this.toolBar = new AMap.ToolBar({
        position: {
          top: '110px',
          right: '40px'
        }
      });
      //
      this.controlBar = new AMap.ControlBar({
        position: {
          top: '10px',
          right: '10px',
        }
      });
      //
      this.overview = new AMap.HawkEye({
        opened: false
      });
      //
      this.satellite = new AMap.TileLayer.Satellite();
      this.satellite.hide();
      //
      this.contextMenu = new AMap.ContextMenu();
      //
      this.mouseTool = new AMap.MouseTool(this.map);
      this.mouseTool.show = () => {
        this.measureToolShow = true;
        this.changeTool("rule");
      };
      this.mouseTool.hide = () => {
        this.mouseTool.close(true);
        this.measureToolShow = false;
      }

      //
      this.contextMenu.addItem("", () => {
        this.map.zoomIn();
      }, 0);
      //
      this.contextMenu.addItem("", () => {
        this.map.zoomOut();
      }, 1);
      //Marker
      this.contextMenu.addItem("", () => {
        let marker = new AMap.Marker({
          map: this.map,
          position: this.contextMenuPositon, //
          draggable: true,
          cursor: 'move',
        });
        marker.on("rightclick", (e) => {
          this.coverMenu.open(this.map, e.lnglat)
          this.selectedCovering = marker;
        });
        let name = 'P' + this.makerMap.size;
        marker.on('dragging', (e) => {
          e.target.setTitle(':' + marker._position.toString());
        });
        marker.setLabel({
          direction: 'right',
          offset: new AMap.Pixel(0, -15),
          content: "<span>" + name + "</span>"
        })
        marker.setTitle(':' + this.contextMenuPositon.toString());
        this.makerMap.set(name, marker);
        this.contextMenu.close();
      }, 3);
      //
      this.coverMenu = new AMap.ContextMenu();
      this.coverMenu.addItem('', () => {
        this.selectedCovering.remove();
        this.coverMenu.close();
      });
      this.coverMenu.addItem('', () => {
        let str = "(" + this.selectedCovering._position.toString() + ")";
        this.$message.info(str);
        this.coverMenu.close();
      });
      this.satellite.setMap(this.map);
      this.map.addControl(this.scale);
      this.map.addControl(this.toolBar);
      this.map.addControl(this.controlBar);
      this.map.addControl(this.overview);
      this.map.on('click', this.onMapClick)
      this.map.on('rightclick', this.onMapRightClick);
      const auto = new AMap.AutoComplete({
        input: "key-input"
      });
      auto.on("select", this.searchWhere);
    },
    onMapRightClick(e) {
      if (this.measureToolShow) return;
      this.contextMenu.open(this.map, e.lnglat);
      this.contextMenuPositon = e.lnglat;
    },
    onMapClick(e) {
    },
    changeTool(type) {
      this.mouseTool.close(true);
      switch (type) {
        case 'rule': {
          this.mouseTool.rule({
            startMarkerOptions: {//
              icon: new window.AMap.Icon({
                size: new window.AMap.Size(19, 31),//
                imageSize: new window.AMap.Size(19, 31),
                image: "//webapi.amap.com/theme/v1.3/markers/b/start.png"
              }),
              offset: new window.AMap.Pixel(-9, -31)
            },
            endMarkerOptions: {//
              icon: new window.AMap.Icon({
                size: new window.AMap.Size(19, 31),//
                imageSize: new window.AMap.Size(19, 31),
                image: "//webapi.amap.com/theme/v1.3/markers/b/mid.png"
              }),
              offset: new window.AMap.Pixel(-9, -31)
            },
            midMarkerOptions: {//
              icon: new window.AMap.Icon({
                size: new window.AMap.Size(19, 31),//
                imageSize: new window.AMap.Size(19, 31),
                image: "//webapi.amap.com/theme/v1.3/markers/b/mid.png"
              }),
              offset: new window.AMap.Pixel(-9, -31)
            },
            lineOptions: {//
              strokeStyle: "solid",
              strokeColor: "#FF33FF",
              strokeOpacity: 1,
              strokeWeight: 2
            }
            // RangingTool   
          });
          break;
        }
        case 'measureArea': {
          this.mouseTool.measureArea({
            strokeColor: '#80d8ff',
            fillColor: '#80d8ff',
            fillOpacity: 0.3
            // Polygon  Option 
          });
          break;
        }
      }
    },
    searchNear(ev) {
      let val = ev.target.value;
      let zoom = this.map.getZoom(); //
      let center = this.map.getCenter(); //
      //
      this.map.getCity(city => {
        let placeSearch = new window.AMap.PlaceSearch({
          map: this.map,
          city: city.citycode,
          citylimit: true,
          panel: "panel",
          autoFitView: true
        });
        placeSearch.searchNearBy(val, [center.lng, center.lat], 5000);
      });
    },
    searchWhere(ev) {
      let placeSearch;
      placeSearch = new window.AMap.PlaceSearch({
        map: this.map,
        children: 1,
        citylimit: true,
        autoFitView: true
      });
      placeSearch.setCity(ev.poi.adcode);
      placeSearch.search(ev.poi.name);
    },
    addCircle() {
      let circle = new window.AMap.Circle({
        center: new window.AMap.LngLat("104.065100", "30.657236"), // 
        radius: 1000,  //
        strokeColor: "#F33",  //
        strokeOpacity: 1,  //
        strokeWeight: 3,  //
        fillColor: "#ee2200",  //
        fillOpacity: 0.35 //
      });
      this.map.add(circle);
      this.map.setFitView();
    },
    toggleSome(ev, name) {
      let checkbox = ev.target;
      if (checkbox.checked) {
        this[name].show();
      } else {
        this[name].hide();
      }
    }
  }
}
</script>

<style scoped>
#container {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

/deep/ .amap-marker-label {
  border: unset;
  padding: unset;
  background-color: unset;
}

.measureTool {
  position: absolute;
  background-color: white;
  bottom: 0;
  right: 170px;
  padding: 3px;
  /*border: 1px solid black;*/
  box-shadow: 0 0 1px 2px #dedff4;
}

.input-card {
  position: absolute;
  background-color: white;
  box-shadow: 0 0 1px 2px #dedff4;
  width: 140px;
  top: 50px;
  left: 20px;
  bottom: auto;
  height: 130px;
}

#key-input {
  width: 250px;
  height: 30px;
}

.searchBox {
  position: absolute;
  background-color: white;
  box-shadow: 0 0 1px 2px #62c46f;
  top: 50px;
  left: 40%;
}
</style>
