<template>
  <AppBorder :app-info=FileList :size=size>
    <template v-slot:header>
      <div class="fx-ac fx-pj" style="width: 100%">
        <span class="fz22 fx-pac">{{ FileList.icon }}</span>
        <span style="color: white"></span>
        <span></span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver fileListBox">
        <el-table
            :data="tableData"
            max-height="600"
            border
            style="width: 100%">
          <el-table-column
              v-for="(col,idx) of tableCol"
              :key=idx
              :prop=col.prop
              :label=col.label
              :width=col.width>
          </el-table-column>
          <el-table-column
              prop=""
              label=""
              width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="download(scope.row)"></el-button>
              <el-button v-if="scope.row.isVideo" type="text" size="small"
                         @click="playVideo(scope.row)">
              </el-button>
              <el-button v-if="scope.row.isVideo" type="text" size="small"
                         @click="cast2Fmp4(scope.row)">FMP4
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import pageManager from "@/utils/PageManager";
import client from '../../utils/chatRoom/ConnectWebSocket'
import MsgType from "@/utils/chatRoom/MsgType";

export default {
  name: "FileList",
  components: {AppBorder},
  data() {
    return {
      FileList: pageManager.getAppInfo("FileList"),
      size: {width: "1000px", height: "400px"},
      tableCol: [
        {prop: "sourceName", label: "", width: 220},
        {prop: "md5", label: "md5Hex", width: 220},
        {prop: "fileSize", label: "(KB)", width: 140},
        {prop: "uploader", label: "", width: 140},
      ],
      tableData: [{
        sourceName: '',
        md5: '',
        uploader: '',
      }],
      curPage: 1,
      pageSize: 12
    }
  },
  created() {
    this.getFileInfo();
  },
  methods: {
    isVideo(name) {
      let videoType = ['mp4', 'flv', 'mov', 'avi', 'wmv', 'mkv'];
      let suffix = name.substring(name.lastIndexOf(".") + 1, name.length);
      return videoType.includes(suffix);
    },
    getFileInfo() {
      this.$http.post("/api/fileInfo", {
        curPage: this.curPage,
        pageSize: this.pageSize
      }).then(res => {
        this.tableData = res.data.map(v => {
          v.fileSize = Math.round(v.fileSize / 1024);
          v.isVideo = this.isVideo(v.sourceName);
          return v;
        });
      }).catch(err => {
        console.log(err)
      });
    },
    playVideo(item) {
      pageManager.playVideoByInfo(item);
    },
    cast2Fmp4(item) {
      if (client.client === null) {
        this.$message.error("WebSocket");
        return;
      }
      let Msg = {
        msg: item.md5,
        destination: "sys",
        type: MsgType.CAST_FMP4
      }
      let notify = this.$notify.info({
        title: '',
        message: '' + item.sourceName + 'Fmp4...',
        duration: 0
      });
      client.sendData(Msg, (Msg) => {
        if (Msg.msg === 'ok') {
          notify.close();
          this.$message.success("");
        } else {
          this.$message.error(Msg.msg);
          notify.close();
        }
      });
    },
    download(item) {
      this.$http.srcGet("/api/download", {
        params: {md5: item.md5}
      }).then(res => {
        console.log('====>', typeof res.data);
        let blob = new Blob([res.data], {
          type: 'application/octet-stream'
        });
        const link = document.createElement('a');
        link.href = window.URL.createObjectURL(blob);
        let filename = res.headers["content-disposition"].split(";")[1].split("filename=")[1];
        link.download = decodeURIComponent(filename);
        link.click();
        //
        window.URL.revokeObjectURL(link.href);
      }).catch(err => {
        console.log(err);
      })
    }
  }
}
</script>

<style scoped>
.fileListBox {

}
</style>