<script>
import AppBorder from "@/components/AppBorder";
import PageManager from '@/utils/PageManager'
import AudioHandler from '@/utils/lyre/AudioHandler'

export default {
  name: "Lyre",
  components: {AppBorder},
  created() {
    AudioHandler.initAudio();
  },
  mounted() {
    AudioHandler.bindClickEvent();
  },
  computed: {},
  data() {
    return {
      Lyre: PageManager.getAppInfo('Lyre'),
      dialogVisible: false,
      script: "",
      HZ: 600,
      idx: 0,
      list: [],
      stopPlay: false,
      size: {width: '1400px', height: '500px'}
    }
  },
  methods: {
    onKeyDown(ev) {
      AudioHandler.play(ev.key);
    },
    openDialog() {
      this.dialogVisible = true;
    },
    play() {
      if (this.idx === this.list.length || this.stopPlay) return;
      let v = this.list[this.idx];
      let match = v.trim() !== "";
      if (match) AudioHandler.play(v.toLocaleLowerCase());
      this.idx++;
      setTimeout(() => {
        this.play();
      }, match ? this.HZ : 200)
    },
    stop() {
      this.stopPlay = true;
    },
    startPlay() {
      this.idx = 0;
      this.stopPlay = false;
      this.list = this.script.trim().split("");
      console.log(this.list);
      this.play();
      this.dialogVisible = false;
    }
  }
}
</script>

