<template>
  <div>
    <div v-if="!item.self" class="flex">
      <div class="fx-pc" style="width: 50px;">
        <HeadImg :item="headImgMap[item.clientName]"></HeadImg>
      </div>
      <div class="fx-ver" style="width: 100%">
        <div style="height: 20px;">
          <span class="fz12">{{ nickNameMap[item.clientName] || item.clientName }}</span>
        </div>
        <div>
          <div class="msg">
            <img v-if="item.img" :src="item.img" alt="" width="100%">
            <a v-else-if="item.file" :href="item.file" @click="download($event,item)">{{ item.msg.fileName }}</a>
            <span v-else style="white-space:pre-wrap;">{{ item.msg }}</span>
          </div>
        </div>
      </div>
    </div>
    <div v-if="item.self" class="flex">
      <div class="msgBox fx-pe fx-ac">
        <div class="msg fr">
          <img v-if="item.img" :src="item.img" alt="" width="90%">
          <a v-else-if="item.file" :href="item.file" @click="download($event,item)">{{ item.msg.fileName }}</a>
          <span v-else style="white-space:pre-wrap;">{{ item.msg }}</span>
        </div>
      </div>
      <div class="fx-pc" style="width: 50px;" @click="openDialog">
        <HeadImg :item="headImgMap[item.clientName]"></HeadImg>
      </div>
    </div>
  </div>
</template>

<script>
import client from "../../utils/chatRoom/ConnectWebSocket";
import HeadImg from "./HeadImg";
import pageManager from "@/utils/PageManager";

export default {
  name: "Message",
  props: ['item'],
  components: {HeadImg},
  data() {
    return {
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
    }
  },
  methods: {
    async download(ev, item) {
      ev.preventDefault();
      if (typeof item.file !== "string") return;
      let fileName = item.msg.fileName;
      if (fileName.endsWith("mp4") || fileName.endsWith("flv")) {
        let down = await this.handleVideo(fileName, item);
        if (!down) return;
      }
      const link = document.createElement('a');
      link.href = item.file;
      link.download = fileName;
      link.click();
      URL.revokeObjectURL(item.file);
      link.remove();
    },
    async handleVideo(fileName, item) {
      try {
        await this.$confirm('? ');
        if (fileName.endsWith("flv")) {
          pageManager.playVideo(item.file, "flv");
        } else {
          if (item.file.startsWith("blob")) {
            let canPlay = await this.analyzeMp4Codec(item);
            if (canPlay) {
              pageManager.playVideo(item.file, "mp4");
            } else {
              this.$message.warning('MP4~');
              return true;
            }
          } else {
            pageManager.playVideoByInfo(item.msg);
          }
        }
        return false
      } catch (reason) {
        console.log(reason);
        return true;
      }
    },
    analyzeMp4Codec(item) {
      return new Promise((resolve, reject) => {
        const MP4Box = require('mp4box');
        const mp4BoxFile = MP4Box.createFile();
        mp4BoxFile.error = (e) => {
          console.log(e);
          this.$message.error('MP4')
        };
        mp4BoxFile.onReady = (info) => {
          let b = MediaSource.isTypeSupported(info.mime);
          resolve(b);
        };
        item.blob.arrayBuffer().then((buf) => {
          buf.fileStart = 0;
          mp4BoxFile.appendBuffer(buf);
          mp4BoxFile.flush();
        }).catch(e => {
          reject(e);
        })
      })
    },
    openDialog() {
      this.$parent.$parent['openDialog']('headImg');
    }
  }
}
</script>

<style scoped>
@import "../../assets/chatroom.css";
</style>