class AudioHandler {
  constructor() {
    this.audioContext = new AudioContext();
    this.keyMap = {
      "a": "1",
      "s": "2",
      "d": "3",
      "f": "4",
      "g": "5",
      "h": "6",
      "j": "7",
      
      "q": "1+",
      "w": "2+",
      "e": "3+",
      "r": "4+",
      "t": "5+",
      "y": "6+",
      "u": "7+",
      
      "z": "1-",
      "x": "2-",
      "c": "3-",
      "v": "4-",
      "b": "5-",
      "n": "6-",
      "m": "7-",
    };
    this.baseUrl = 'http://121.89.232.86/Instruments/windsong_lyre/';
    this.audioBufferMap = {};
  }
  
  initAudio() {
    for (let key in this.keyMap) {
      fetch(this.baseUrl + this.keyMap[key] + '.mp3')
        .then(response => response.arrayBuffer())
        .then(arrayBuffer => this.audioContext.decodeAudioData(arrayBuffer))
        .then(audioBuffer => {
          this.audioBufferMap[key] = audioBuffer;
        });
    }
  }
  
  release(key, keyMap) {
    let note = keyMap[key];
    if (note != null) {
      document.getElementById(key).parentNode.classList.remove('key-press');
    }
  }
  
  play(key) {
    if (this.audioBufferMap[key]) {
      //
      let spread = document.getElementById(key).parentNode.childNodes[0];
      if (spread.getAttribute('id') === 'spread1') {
        spread.setAttribute('id', 'spread2');
      } else {
        spread.setAttribute('id', 'spread1');
      }
      setTimeout(() => {
        this.release(key, this.keyMap)
      }, 100);
      // 
      const source = this.audioContext.createBufferSource();
      source.buffer = this.audioBufferMap[key];
      source.connect(this.audioContext.destination);
      source.start();
    }
  }
  
  bindClickEvent() {
    for (let key in this.keyMap) {
      document.getElementById(key).addEventListener('click', () => {
        this.play(key)
      });
    }
  }
}

const audioHandler = new AudioHandler();
export default audioHandler;