<template>
  <AppBorder :app-info=Video :size=size herder-css="header2">
    <template v-slot:header>
      <div class="fx-ac fx-pj" style="width: 100%">
        <span style="color: white">FLV</span>
        <span class="fx-ac" style="color: white">
          URL:<input type="text" v-model="url" style="width: 400px" @mousemove="(ev)=>{ev.stopPropagation()}"/>
        </span>
        <span>
          <span class="fz20" @click="play">{{ isPlaying ? '' : '' }}</span>
          <span class="fz20" @click="flv_destroy"></span>
        </span>
        <span></span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver videoBox">
        <video id="videoElement" controls @timeupdate="onTimeupdate" @pause="pauseEvent" @play="playEvent"></video>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import flvJs from 'flv.js'
import http from '@/axios'
import PageManager from '@/utils/PageManager'
import pageManager from "@/utils/PageManager";
import client from "@/utils/chatRoom/ConnectWebSocket";
import MsgType from "@/utils/chatRoom/MsgType";

export default {
  name: "Video",
  components: {AppBorder},
  mounted() {
    window.video = this;
    this.videoElement = document.getElementById('videoElement');
  },
  data() {
    return {
      name: "Video",
      Video: PageManager.getAppInfo("Video"),
      size: {
        width: '800px',
        height: '600px'
      },
      flvPlayer: null,
      isPlaying: false,
      url: http.LiveBaseUrl + '/live?port=1935&app=myapp&stream=mystream',
      type: 'flv',
      mediaSource: null,
      sourceBuffer: null,
      duration: 0,
      continueLoad: true,
      allowLoadTime: -1,
      start: 0,
      segSize: 0,
      md5: null,
    }
  },
  methods: {
    prePlay(item) {
      this.$http.get("/api/mp4Info", {
        md5: item.md5
      }).then(ret => {
        let {isFmp4, codec, index, duration} = ret.data
        console.log(ret.data);
        if (!MediaSource.isTypeSupported(codec)) {
          this.$message.error(",,");
          pageManager.closeApp(this.name);
          return
        }
        if (isFmp4) {
          this.md5 = item.md5;
          this.duration = duration;
          this.playMp4Seg(index, codec, item);
        } else {
          this.$confirm(",")
              .then(v => {
                this.setUrl(this.$http.HttpBaseUrl + item.url, "mp4");
              }).catch(e => {
          });
        }
      }).catch(err => {
        console.log(err);
        pageManager.closeApp(this.name);
      });
    },
    playMp4Seg(indexList, codec, item) {
      if ('MediaSource' in window && MediaSource.isTypeSupported(codec)) {
        this.mediaSource = new MediaSource();
        //Blob URL, flvJsBlobMp4
        let url = URL.createObjectURL(this.mediaSource);
        this.setUrl(url, "mp4");
        this.mediaSource.addEventListener('sourceclose', (ev) => {
          console.log('sourceclose:', ev.target);
        });
        this.mediaSource.addEventListener('sourceended', (ev) => {
          console.log('sourceended:', ev.target);
        });
        this.mediaSource.addEventListener('sourceopen', () => {
          console.log(':', item.sourceName, '', indexList.length, '');
          const chunkTotal = indexList.length;
          let i = 1;
          this.sourceBuffer = this.mediaSource.addSourceBuffer(codec);
          this.mediaSource.duration = this.duration / 1000;
          this.sourceBuffer.addEventListener('updateend', (ev) => {
            console.log('mp4', i);
            if (this.sourceBuffer.buffered.length === 1) {
              this.allowLoadTime = this.sourceBuffer.buffered.end(0) - 10;
              this.continueLoad = this.videoElement.currentTime > this.allowLoadTime;
            }
            if (i <= chunkTotal) {
              //
              this.start = indexList[i - 1]['fragmentStart'];
              this.segSize = indexList[i - 1].size;
              if (this.continueLoad) this.getNextVideoSeg(this.start, this.segSize, item.md5);
            }
            if (i > chunkTotal) {
              //
              console.log("");
              this.mediaSource.endOfStream();
            }
            i++;
          });
          this.getNextVideoSeg(0, indexList[0]['fragmentStart'], item.md5);
          // this.initPlay();
        }); //
      } else {
        //
        this.$message.error('Unsupported MIME type or codec: ', codec);
      }
    },
    onTimeupdate(ev) {
      const video = ev.target;
      if (video.currentTime <= this.allowLoadTime && this.continueLoad) {
        this.continueLoad = false;
      } else if (video.currentTime > this.allowLoadTime && !this.continueLoad) {
        this.continueLoad = true;
        this.getNextVideoSeg(this.start, this.segSize, this.md5);
      }
    },
    initPlay() {//
      this.$http.srcGet("/api/initVideo", {
        responseType: 'arraybuffer',
        params: {
          filePath: "D:\\Projects\\hp-netty-server\\fragmentMp42\\init.mp4"
        }
      }).then(res => {
        console.log(res);
        this.sourceBuffer.appendBuffer(res.data);
      }).catch(err => {
        console.log(err);
      })
    },
    getNextVideoSeg(start, chunkSize, md5) {
      if (client.client !== null) {
        let Msg = {
          msg: {
            md5,
            start,
            chunkSize,
          },
          destination: "sys",
          type: MsgType.Get_FMP4
        }
        client.sendData(Msg, (argv) => {
          if (argv instanceof Blob)
            argv.arrayBuffer().then(v => {
              this.sourceBuffer.appendBuffer(v);
            }).catch(err => {
              console.log(err);
            });
          else {
            this.$message.error(argv);
          }
        });
      } else {
        this.$http.srcGet("/api/getMp4Segment", {
          responseType: 'arraybuffer',
          params: {
            start, chunkSize, md5
          }
        }).then(res => {
          this.sourceBuffer.appendBuffer(res.data)
        }).catch(err => {
          console.log(err);
        });
      }
    },
    parseUrl(url) {
      let source = {type: this.type, isLive: false, url};
      if (url.startsWith('blob')) return source;
      if (url.endsWith("mp4")) {
        source.type = "mp4";
      } else if (url.endsWith("flv")) {
        source.type = "flv";
      } else {
        source.isLive = true;
      }
      if (!url.startsWith("http")) source.url = "http://" + url;
      return source;
    },
    createFlvPlayer(url) {
      if (flvJs.isSupported()) {
        let source = this.parseUrl(url);
        this.flvPlayer = flvJs.createPlayer(source);
        this.flvPlayer.attachMediaElement(this.videoElement);
        this.flvPlayer.load();
      } else {
        alert("???!!!");
      }
    },
    pauseEvent() {
      this.isPlaying = false;
    },
    playEvent() {
      this.isPlaying = true;
    },
    play() {
      if (this.isPlaying) {
        this.flv_pause();
      } else this.flv_start();
    },
    flv_start() {
      if (this.flvPlayer == null) this.createFlvPlayer(this.url);
      this.flvPlayer.play();
    },
    flv_pause() {
      this.flvPlayer.pause();
    },
    flv_destroy() {
      this.isPlaying = false;
      this.flvPlayer.pause();
      this.flvPlayer.unload();
      this.flvPlayer.detachMediaElement();
      this.flvPlayer.destroy();
      this.flvPlayer = null;
    },
    setUrl(url, type) {
      this.url = url;
      this.type = type;
      this.play();
    }
  }
}
</script>

<style scoped>
.videoBox {
  border: 1px saddlebrown solid;
}
</style>