<template>
  <AppBorder :app-info="Clipboard" :herder-css="'header1'" :size=size>
    <template v-slot:header>
      <div class="fx-pj fx-ac" style="width: 100%">
        <span class="fz22 fx-pac">{{ Clipboard.icon }}</span>
        <span>
        <el-dropdown @command="changeGroup">
          <span class="el-dropdown-link">
            : <input type="text" v-model="group" style="width: 45px" @mousemove="stopPropagation"
                        @mousedown="stopPropagation"/><i
              class="el-icon-arrow-down"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item,key,idx) in contentMap" :key=idx :command=key>{{ key }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <button type="button" @click="addGroup"></button>
        <button type="button" @click="delGroup"></button>
        <button type="button" @click="updateGroup"></button>
        </span>
        <span></span>
        <button type="button" @click="addRow"></button>
      </div>
    </template>
    <template v-slot:main>
      <div class="tableBox">
        <el-table
            :data="tableData"
            style="width: 100%">
          <el-table-column
              prop="content"
              label=""
              width="500">
            <template slot-scope="scope">
              <!--              <el-input v-model="scope.row['content']" :type="'textarea'" :autosize="true"></el-input>-->
              <textarea name="text" id="text" cols="50" rows="2" v-model="scope.row['content']"
                        @blur="autoAdd" @mousemove="stopPropagation"></textarea>
            </template>
          </el-table-column>
          <el-table-column
              label=""
              width="100">
            <template slot-scope="scope">
              <el-button @click="copy(scope.row)" type="text" size="small"></el-button>
              <el-button @click="del(scope.$index)" type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import PageManager from '@/utils/PageManager'
import AppBorder from "@/components/AppBorder";

export default {
  name: "Clipboard",
  components: {AppBorder},
  data() {
    return {
      Clipboard: PageManager.getAppInfo('Clipboard'),
      tableData: [],
      group: "null",
      lastVisit: "null",
      contentMap: {"default": [{content: "[UNK]"}, {content: "[DIA]"}, {content: ""}]},
      size: {width: '620px', height: '500px'}
    }
  },
  created() {
    let parse = JSON.parse(localStorage.getItem("contentMap"));
    if (parse instanceof Object) this.contentMap = parse;
  },
  methods: {
    stopPropagation(ev) {
      ev.stopPropagation();
    },
    changeGroup(e) {
      this.lastVisit = this.group = e;
      this.tableData = this.contentMap[e];
    },
    updateGroup() {
      if (this.contentMap[this.group]) {
        this.$message.error("");
        return;
      }
      this.contentMap[this.group] = this.tableData;
      this.$delete(this.contentMap, this.lastVisit);
      this.sycn2Storage();
      this.$message.success("");
    },
    copy(item) {
      this.$copyText(item.content).then((e) => {
        this.$message.success("");
      }, (e) => {
        this.$message.error("");
      });
    },
    addRow() {
      this.tableData.push({content: ""});
      this.sycn2Storage();
    },
    addGroup() {
      if (this.contentMap[this.group]) {
        this.$message.error("");
        return;
      }
      this.contentMap[this.group] = [{content: ""}];
      this.changeGroup(this.group);
      this.sycn2Storage();
    },
    delGroup() {
      this.$confirm('').then(v => {
        this.$delete(this.contentMap, this.group);
        this.group = "null";
        this.tableData = [];
        this.sycn2Storage();
      });
    },
    del(idx) {
      this.tableData.splice(idx, 1);
      this.sycn2Storage();
    },
    autoAdd() {
      if (this.tableData[this.tableData.length - 1].content !== "") {
        this.addRow();
      }
    },
    sycn2Storage() {
      localStorage.setItem('contentMap', JSON.stringify(this.contentMap));
    }
  }
}
</script>

<style scoped>
.inputContent {
  width: 500px;
}

.tableBox {
  height: 800px;
  overflow-y: scroll;
  border: 1px saddlebrown solid;
}

:deep(.el-table .el-table__cell) {
  padding: 1px 0;
  min-width: 0;
  box-sizing: border-box;
  text-overflow: ellipsis;
  vertical-align: middle;
  position: relative;
  text-align: left;
}
</style>
